/*--------------------------------------------------------------
# Footer
--------------------------------------------------------------*/
#colophon {
  background-color: #394ba0;
  color: #fff;
  max-width: 137.5em;
  margin-left: auto;
  margin-right: auto; }
  @media screen and (min-width: 46.875em) {
    #colophon {
      font-size: .885rem; } }
  #colophon .site-info {
    padding-left: 1.875rem;
    padding-right: 1.875rem;
    padding-top: 2rem;
    padding-bottom: 2rem;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; }
    @media screen and (min-width: 46.875em) {
      #colophon .site-info {
        padding-left: 2.25rem;
        padding-right: 2.25rem; } }
    @media screen and (min-width: 75em) {
      #colophon .site-info {
        padding-left: 3.5rem;
        padding-right: 3.5rem; } }
  #colophon span.title {
    display: block;
    color: #ff7439;
    text-transform: uppercase;
    font-family: "Roboto-Bold", sans-serif;
    font-size: 1.3em;
    letter-spacing: 1px;
    line-height: 1.3; }
  #colophon .contact,
  #colophon .location-copyright,
  #colophon .copyright-section,
  #colophon .phone-section,
  #colophon .email-section,
  #colophon .hours-section {
    margin-top: 2rem; }
  @media screen and (min-width: 59.375em) {
    #colophon .contact,
    #colophon .phone-section,
    #colophon .location-copyright {
      margin-top: 0; } }
  @media screen and (min-width: 59.375em) {
    #colophon .column.contact,
    #colophon .column.location-copyright {
      flex: 3; } }
  @media screen and (min-width: 90em) {
    #colophon .column.contact,
    #colophon .column.location-copyright {
      flex: 2; } }
  #colophon .column {
    flex: 0 0 100%; }
    @media screen and (min-width: 59.375em) {
      #colophon .column {
        flex: 1;
        padding: 10px; } }
  #colophon .branding {
    flex: 1 1 30%; }
    #colophon .branding svg {
      width: 140px;
      height: 65px; }
    @media screen and (min-width: 59.375em) {
      #colophon .branding {
        flex: 1; } }
  #colophon .social {
    flex: 1 1 30%;
    margin-top: 0; }
    @media screen and (min-width: 59.375em) {
      #colophon .social {
        flex: 0 0 auto;
        order: 5; } }
  @media screen and (min-width: 59.375em) {
    #colophon .branding,
    #colophon .footer-nav {
      flex: 0 0 auto;
      padding-right: 2em; } }
  @media screen and (min-width: 75em) {
    #colophon .branding,
    #colophon .footer-nav {
      flex: 0 0 auto;
      padding-right: 4em; } }
  #colophon #menu-footer-menu {
    display: none;
    list-style-type: none;
    margin: 0;
    padding: 0;
    text-transform: uppercase;
    font-family: "Roboto-Bold", sans-serif;
    letter-spacing: 1px; }
    @media screen and (min-width: 59.375em) {
      #colophon #menu-footer-menu {
        display: block; } }
    #colophon #menu-footer-menu a {
      text-decoration: none;
      color: #fff; }
      #colophon #menu-footer-menu a:before {
        content: '\002D';
        margin-right: .5rem; }
  #colophon .phone-section a {
    text-decoration: none;
    color: #fff; }
  #colophon .email-section a {
    text-decoration: none;
    color: #fff; }
  #colophon .hours-section p {
    margin: 0 0 .25em; }
  #colophon .copyright-section {
    margin-top: 4rem !important; }
    #colophon .copyright-section p {
      margin: 0 0 .75em; }
